<template>
  <v-container
    v-if="logado === 'true' ? true : false"
    fluid
    tag="section"
  >
    <v-overlay :value="showUpdate">
      <v-alert
        icon="mdi-check"
        prominent
        shaped
        type="success"
        transition="scale-transition"
        class="position-fixed"
      >
        Usuário cadastrado com sucesso!
      </v-alert>
    </v-overlay>
    <v-overlay :value="showError">
      <v-alert
        icon="mdi-alert"
        prominent
        shaped
        type="error"
        transition="scale-transition"
        class="position-fixed"
      >
        Algo de errado não está certo.
      </v-alert>
    </v-overlay>

    <v-row justify="center">
      <v-col
        cols="12"
        sm="10"
        md="8"
      >
        <v-card>
          <v-form
              id="form"
              ref="form"
              v-model="valid"
              method="POST"
              action="https://sheetdb.io/api/v1/khm5ici88y1u4"
            >
            <v-row>
              <v-col
                cols="12"
                sm="4"
                md="6"
              >
                <h3 class="font-weight-medium mt-6 ms-6">
                  Cadastrar Usuário — <small class="text-body-1">Insira os dados</small>
                </h3>
              </v-col>
              <v-col
                cols="12"
                sm="8"
                md="6"
              >
                <v-autocomplete
                  v-model="items.profile"
                  :items="profiles"
                  auto-select-first
                  hide-details
                  clearable
                  outlined
                  name="data[PERFIL]"
                  label="Perfil"
                  class="ma-3"
                  item-text="text"
                  item-value="value"
                />
              </v-col>
            </v-row>

            <v-container>
              <v-row>
                <v-col
                  cols="6"
                  sm="4"
                  class="mb-n8"
                >
                  <v-text-field
                    v-model="items.user"
                    :rules="[() => items.user.length >= 2 || 'Campo Obrigatório']"
                    color="blue"
                    name="data[COR]"
                    label="Nome"
                    outlined
                  />
                </v-col>
                <v-col
                  cols="6"
                  sm="4"
                  class="mb-n8"
                >
                  <v-text-field
                    v-model="items.sobrenome"
                    :rules="[() => items.sobrenome.length >= 2 || 'Campo Obrigatório']"
                    color="blue"
                    name="data[NOMES]"
                    label="Sobrenome"
                    outlined
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="4"
                  class="mb-n8"
                >
                  <v-text-field
                    v-model="items.rua"
                    v-mask="'###.###.###-##'"
                    :rules="rulesCPF"
                    name="data[RUA]"
                    color="blue"
                    label="CPF"
                    outlined
                  />
                </v-col>
              </v-row>

              <v-row>
                <v-col
                  cols="12"
                  sm="6"
                >
                  <v-text-field
                    v-model="items.email"
                    hide-details
                    color="blue"
                    name="data[EMAIL]"
                    label="Endereço de E-mail"
                    outlined
                  />
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                >
                  <v-autocomplete
                    v-model="company"
                    :items="companies"
                    auto-select-first
                    hide-details
                    clearable
                    outlined
                    label="Empresa"
                    item-text="name"
                    item-value="name"
                  />
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="6">
                  <v-text-field
                    v-model="items.cep"
                    v-mask="'########'"
                    :rules="rulesCEP"
                    name="data[CEP]"
                    hide-details
                    color="blue"
                    label="CEP"
                    outlined
                    @keyup="preencherCep"
                  />
                </v-col>
                <v-col cols="3">
                  <v-text-field
                    v-model="items.numero"
                    hide-details
                    color="blue"
                    name="data[NUMERO]"
                    label="Nº"
                    outlined
                  />
                </v-col>
                <v-col cols="3">
                  <v-text-field
                    v-model="items.estado"
                    hide-details
                    color="blue"
                    label="Estado"
                    outlined
                    disabled
                  />
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="6">
                  <v-text-field
                    v-model="items.logradouro"
                    color="blue"
                    label="Logradouro"
                    hide-details
                    outlined
                    disabled
                  />
                </v-col>
                <v-col cols="6">
                  <v-text-field
                    v-model="items.cidade"
                    color="blue"
                    label="Cidade"
                    hide-details
                    outlined
                    disabled
                  />
                </v-col>
              </v-row>

              <v-row>
                <v-col class="text-right">
                  <v-btn
                    color="primary"
                    min-width="150"
                    :disabled="!valid"
                    @click="register"
                  >
                    Salvar
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'UserRegisterView',

    data: () => ({
      erroCep: false,
      showUpdate: false,
      showError: false,
      valid: true,
      company: '',
      logado: window.localStorage.logado,
      companies: [],
      profiles: [
        { value: 'ADMIN', text: 'Administrador' },
        { value: 'CUSTOMER', text: 'Transportador' },
        { value: 'FINANCIAL', text: 'Financeiro' },
        { value: 'MASTER', text: 'Master' }
      ],
      items: {
        logradouro: '',
        estado: '',
        cidade: '',
        rua: '',
        user: '',
        sobrenome: '',
        email: '',
        cep: '',
        numero: '',
        profile: '',
      },
    }),

    computed: {
      rulesCEP () {
        const rules = []
        const rule = ( !this.items.cep || this.erroCep === false && this.items.cep.length === 8) || 'CEP inválido'
        rules.push(rule)

        return rules
      },
      rulesCPF () {	
        let cpf = this.items.rua
        cpf = cpf.replace(/[^\d]+/g,'')
        let add1 = 0
        let add2 = 0
        let rev = 0
        let i
        let for1 = true
        let for2 = true
        // // Valida 1o digito
        for (i=0; i < 9; i ++) {
          add1 += parseInt(cpf.charAt(i)) * (10 - i)
          rev = 11 - (add1 % 11)
          if (rev === 10 || rev === 11) {
            rev = 0
            if (rev != parseInt(cpf.charAt(9))) {
              for1 = false
            }
          } else {
            for1 = true
          }
        }
        // Valida 2o digito
        for (i = 0; i < 10; i ++) {
          add2 += parseInt(cpf.charAt(i)) * (11 - i)
          rev = 11 - (add2 % 11)
          if (rev === 10 || rev === 11) {
            rev = 0
            if (rev != parseInt(cpf.charAt(10))) {
              for2 = false
            }
          } else {
            for2 = true
          }
        }
        const rules = []
        const rule = (
          cpf.length === 11 &&
          cpf != "00000000000" &&
          cpf != "11111111111" &&
          cpf != "22222222222" &&
          cpf != "33333333333" &&
          cpf != "44444444444" &&
          cpf != "55555555555" &&
          cpf != "66666666666" &&
          cpf != "77777777777" &&
          cpf != "88888888888" &&
          cpf != "99999999999" &&
          for1 === true &&
          for2 === true ||
          'CPF inválido'
        )
        rules.push(rule)

        return rules
      }
    },

    beforeMount () {
      this.preencherCep()
    },

    methods: {
      validateField () {
        this.$refs.form.validate()
      },
      getCep (cep) {
        return axios.get(`https://viacep.com.br/ws/${cep}/json/`)
      },
      // getCompanies () -> fetch empresas, popula companies com o RAZAOSOCIAL como text e EMPRESAID como value
      preencherCep () {
        const cep = this.items.cep.replace(/\D/g, '')
        if (cep.length === 8) {
          this.getCep(cep)
            .then(r => {
              this.items.logradouro = r.data.logradouro
              this.items.estado = r.data.uf
              this.items.cidade = r.data.localidade
              'erro' in r.data ? this.erroCep = true : this.erroCep = false
            })
        }
      },
      register () {
        var form = document.getElementById('form')
        fetch(form.action, {
          method: 'POST',
          body: new FormData(form),
        })
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 3500)
            } else {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
              }, 3500)
            }
          })
      },
    },
  }
</script>
